
















import { Component, Vue, Emit } from 'vue-property-decorator'

@Component({
    components: {
    },
})
export default class ChoiceGhost extends Vue {
  private page: number = 0 // index
  private count: number = 18 // 
  private changeBG: boolean = false // 

  private ghostsDetail: any = [
      {
          randomName: ['', '', '', '', '', '', '', '+1', '', '']
      },
      {
          randomName: ['', '', '', '', '', '', '', '', '']
      },
      {
          randomName: ['', '', '', 'N', '', '', '', '48', '', '']
      },
      {
          randomName: ['', '', '', '', '', '', '', '', '', '']
      },
      {
          randomName: ['', '', '', '', '', '', '', '', '418', '']
      },
      {
          randomName: ['', '', '', '', '', '', 'EX', '', '', '']
      },
      {
          randomName: ['', ' ', '', '', '', '', '', '', '', '']
      },
      {
          randomName: ['', '  ', '', '', '', '', '', '', '', '']
      },
      {
          randomName: ['', '', '', '', '', '', '', '']
      },
      {
          randomName: ['', '', '', 'High', '', '', '', '', '', '']
      },
      {
          randomName: ['', '', '', '', '', '', '', '', '', '']
      },
      {
          randomName: ['', '', '', '', '', '', '', '', '']
      },
      {
          randomName: ['', 'sex', '', 'Ta', '', '', '', '', '', '']
      },
      {
          randomName: ['', '', '', '', '', '', '', '', '']
      },
      {
          randomName: ['', '', '', '', 'Deadline', 'Deadline', '', '', '']
      },
      {
          randomName: ['', '', '', '', '', '', '', '', '', '']
      },
      {
          randomName: ['', '', '', '', '', '//', '', '', '', '']
      },
      {
          randomName: ['', '', '', '', '', '', '', '', '', '']
      },
  ]

  private ghosts: ghost[] = []
  private backGhosts: ghost[] = []
  private randomNameMode: boolean = false
  private selectGhosts: ghost[] = []

  private addBackGhosts (): void {
      for (let i = 1; i < this.count + 1; i++) {
          const iString = (i + '').padStart(2, '0')
          this.backGhosts.push({
              detail: {
                  randomName: []
              },
              name: '', // 
              index: i,
              select: false,
              img: require(`../assets/GHOSTS/img_ghost_${iString}@3x.png`),
              bg: require(`../assets/GHOSTS/bg_ghost_${iString}@3x.png`),
          } as ghost)
      }
  }

  // ghost  
  private ghostsDetailChange (i, randomMode = false) {
      const ghost = this.backGhosts[i]
      ghost.detail = this.ghostsDetail[i]
      const one = this.$root.configData.type === 'reason' ? 0 : 4
      const two = this.$root.configData.type === 'reason' ? 3 : ghost.detail.randomName.length - 1
      randomMode ? ghost.name = ghost.detail.randomName[this.$wjh.randomNum(one, two)] : ghost.name = ghost.detail.randomName[this.$root.configData.type === 'reason' ? 0 : 4] // 
      this.ghosts.push(this.backGhosts[i])
  }

  private change () {
      // @ts-ignore
      _hmt.push(['_trackEvent', 'nav', 'click', ''])
      this.ghosts = []
      this.$nextTick(() => {
          this.page++
          const allPage: number = Math.floor(this.count / 6) // 
          const last = this.count % 6 // 
          if ((this.page === allPage + 1 && last === 0)) {
              this.page = 1
              this.randomNameMode = true
          }
          if (this.page <= allPage) {
              for (let i = (this.page - 1) * 6; i < (this.page * 6); i++) {
                  this.ghostsDetailChange(i, this.randomNameMode)
              }
              return
          }
          if (this.page === allPage + 1) {
              for (let i = (this.page - 1) * 6; i < ((this.page - 1) * 6) + last; i++) {
                  this.ghostsDetailChange(i, this.randomNameMode)
              }
              this.page = 0
          }
      })
  }

  @Emit('parentChange')
  private selectEvent (data: ghost, event: any) {
      // @ts-ignore
      _hmt.push(['_trackEvent', 'nav', 'click', `ghost=${data.index}`, data.index])
      event.preventDefault()
      if (this.selectGhosts.length >= 5) {
          this.$parent.$parent.showTips('5')
          return
      }
      data.select = true
      setTimeout(() => {
          data.select = false
      }, 1000)
      this.selectGhosts.push(JSON.parse(JSON.stringify(data)))
  }

  private mounted () {
      setTimeout(() => {
          if ((window.innerHeight / window.innerWidth) > 1.8) {
              this.changeBG = true
          }
      }, 300)

      this.addBackGhosts()
      this.change()
  }
}
